<template lang="html">
  <v-app>
    <v-container class="mainContainer margtop" grid-list-xl>
      <v-layout row wrap  class="text-xs-center mb-5"  >
        <v-flex xs12  v-for="pic in pics" :class="size" v-if="(!pic.vert) ? size = 'lg12' : size = 'lg6'">
          <clazy-load  :src="getPic(pic.name)">
            <v-flex  slot="image">
              <img :src='getPic(pic.name)' slot="image" class="guda">
            </v-flex>
            <v-flex  slot="placeholder" class="holder">
            </v-flex>
          </clazy-load>
          <h3 class="">{{title}}</h3>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  props: ['id'],
  data() {
    return {
      size: '',
      title: 'Picture Title'
    }
  },
  computed: {
    album() {
      return this.$store.getters.LoadedAlbum(this.id)
    },
    pics() {
      return this.album.slike
    }

    }
  ,
  methods: {
    getPic(pic) {
      return require(`@/assets/img/${pic}`)
    }
  }
}
</script>

<style scoped lang="css">
.holder{
  width: 100%;
  padding-bottom: 400px;
}
.guda{
  min-height: 714px;
}

</style>



